@import "main-bx";

.exchange {
    padding: 100px 0;
 
    @include below(768px) {
        padding: 25px 0;
    }

    &__wrap {
        display: flex;
        align-items: stretch;
        justify-content: space-between;

        @include below(1024px) {
            flex-direction: column;
        }
    }

    &__img {
        width: 100%;
        max-width: 759px;

        @include below(1240px) {
            max-width: 570px;
        }

        img {
            width: 100%;
        }
    }

    &__left {
        position: relative;

        @include below(1240px) {
            margin-right: 50px;
        }

        @include below(1240px) {
            margin-right: 30px;
        }

        @include below(1024px) {
            margin-right: 0;
            margin-bottom: 20px;
        }
    }

    &__icon {
        width: 74px;
        height: 74px;
        background-color: $light_blue_color;
        border-radius: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 140px;
        right: 90px;
        

        @include below(1240px) {
            width: 60px;
            height: 60px;
        }

        @include below(1024px) {
            width: 45px;
            height: 45px;
            top: 100px;
            right: 180px;
        }

        @include below(665px) {
            top: 80px;
            right: 25px;
            width: 30px;
            height: 30px;
        }

        svg {
            width: 37px;
            min-width: 37px;
            height: 37px;

            @include below(1024px) {
                width: 30px;
                min-width: 30px;
                height: 30px;
            }

            @include below(665px) {
                width: 20px;
                min-width: 20px;
                height: 20px;
            }
        }
    }

    &__title {
        margin-bottom: 10px;
    }

    &__subtitle {
        font-weight: 600;
        font-size: 24px;
        line-height: 160%;
        display: inline-block;
        background-color: $light_blue_color;
        border-radius: 10px;
        padding: 5px 10px;

        @include below(1240px) {
            font-size: 18px;
            line-height: 90%;
        }

        @include below(768px) {
            font-size: 16px;
        }
    }

    &__content {
        padding: 50px;
        background-color: $white_2_color;
        border-radius: 10px;

        @include below(1240px) {
            padding: 30px;
        }

        @include below(500px) {
            padding: 10px;
        }
    }

    &__item {
        display: flex;
        align-items: center;

        &:first-of-type {
            .exchange__desc-text {
                display: flex;
                align-items: center;
            }
        }

        &:not(:last-of-type) {
            margin-bottom: 25px;

            @include below(1240px) {
                margin-bottom: 15px;
            }
        }

        @include below(400px) {
            // flex-direction: column;
            align-items: flex-start;
        }
    }

    &__number {
        width: 40px;
        height: 40px;
        background-color: #404040;
        border-radius: 50%;
        margin-right: 18px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 700;
        color: $white_2_color;
        flex-shrink: 0;

        @include below(400px) {
            margin-right: 10;
            // margin-bottom: 15px;
        }
    }

    &__desc-text {
        svg {
            width: 18px;
            height: 18px;
            margin-right: 10px;
        }

        @include below_and_above(1380px,1024px) {
            white-space: nowrap;
        }
    }

    &__desc-title {
        font-weight: 700;
        margin-bottom: 5px;

        @include below_and_above(1380px,1024px) {
            white-space: nowrap;
        }
    }

    &__btn {
       margin-top: 30px;

       @include below(1024px) {
            margin-top: 20px;
       }

       @include below(768px) {
            padding: 10px 40px;
            width: 100%;
       }
    }

}