@import "main-bx";

.specifications {
 
    &__container {
        padding: 110px 0 103px;
    }

    &__title {
        text-align: center;

        @media (max-width: 1240px), (max-width: 1024px), (max-width: 768px) {
            padding: 30px 0;
        }
    }

    &__content {
        padding: 20px 6.8%;
        margin-bottom: 3px;
        border-bottom: 1px solid #404040;

        @include above(769px) {
            display: flex;
            justify-content: space-between;
        }

        @include below(1024px) {
            padding: 20px;
        }

        @include below(768px) {
            padding: 10px;
        }
    }

    &__list {
        @include above(769px) {
            width: 100%;
            max-width: 520px;
        }


        &:first-child {
            @include above(1025px) {
                margin-right: 13.60%;
            }

            @include below_and_above(1024px, 769px) {
                margin-right: 10%;
            }

            @include below(768px) {
                margin-bottom: 10px;
            }
        }
    }

    &__list-item {
        font-size: 14px;
        line-height: 19px;

        &:not(:last-child) {
            margin-bottom: 10px;
        }

        @include above(420px) {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }

        span {
            &:first-child {
                margin-right: 24px;

                @include below(420px) {
                    display: block;
                    color: #777;
                    margin-bottom: 5px;
                }
            }

            &:last-child {
                @include below(420px) {
                    font-size: 16px;
                    font-weight: 700;
                }
            }
        }
    }
}