@import "main-bx";

.form {

    &__label {
        display: block;
        &:hover {
            .form__input {
                border-color: rgba(0, 0, 0, .3);
            }
        }
    }

    &__input {
        width: 100%;
        padding: 12px 17px;
        border: 1px solid #fff;
        transition: $trns;
        background-color: $white_color;

        &:focus {
            border-color: rgba(0, 0, 0, .3);
        }
    }

    &__btn {
      
    }

    &__checkbox {
        cursor: pointer;
        display: flex;
        align-items: center;
        color: $white_color;
        input {
            display: none;
        }
        input:checked {
            & ~ .form__checkbox-box svg {
                opacity: 1;
            }
        }
    }

    &__checkbox-box {
        width: 24px;
        min-width: 24px;
        height: 24px;
        background-color: $white_color;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 10px;
        border: 1px solid currentColor;

        svg {
            opacity: 0;
            width: 12px;
            height: 12px;
        }
    }

    &__checkbox-text {
        color: currentColor;
        font-size: 12px;
        line-height: 23px;

        @include below(768px) {
            line-height: 1.5;
        }

        a {
            color: currentColor;
            text-decoration: underline;
        }
    }
}