@import "main-bx";

.benefits {
    padding: 100px 0;
    background-color: $white_2_color;

    @include below(768px) {
        padding: 25px 0;
    }

    &__title {
        text-align: center;
        margin-bottom: 40px;

        @include below(768px) {
            margin-bottom: 30px;
        }
    }

    &__wrap {
        display: flex;
        align-items: stretch;
        flex-wrap: wrap;
        margin: -30px -15px 0;

        @include below(1240px) {
            justify-content: center;
            margin: -15px -10px 0;
        }

        @include below(500px) {
            margin: -20px 0 0;
        }
    }

    &__item {
        width: calc(25% - 30px);
        margin: 30px 15px 0;
        display: flex;
        flex-direction: column;
        background-color: $white_color;
        border: 1px solid #eee;
        border-radius: 5px;
        padding: 30px 30px 23px;
        position: relative;

        &::after {
            content: "";
            width: 10px;
            height: 10px;
            background-color: $light_blue_color;
            border-radius: 5px;
            position: absolute;
            top: 30px;
            right: 30px;
        }

        @include below(1240px) {
            margin: 15px 10px 0;
        }

        @include below(1024px) {
            width: calc(50% - 20px);
        }

        @include below(500px) {
            width: 100%;
            margin: 20px 0 0;
            padding: 25px 25px 23px;
        }
    }

    &__item-icon {
        width: 26px;
        min-width: 26px;
        height: 27px;
        margin-bottom: 30px;

        @include below(500px) {
            margin-bottom: 20px;
        }

        img {
            height: auto;
            width: 100%;
        }
    }

    &__item-title {
        margin-bottom: 5px;

        @include above(1380px) {
            white-space: nowrap;
        }
    }
}