@import "main-bx";

.intro {
    background-image: url("../img/content/intro/intro_banner01.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;

    &__img {
        img {
            width: 100%;
        }
    }
    
    &__content {
        padding: 60px 0 480px;
        text-align: center;

        @include below(1024px) {
            padding: 60px 0 300px;
        }

        @include below(768px) {
            padding: 40px 0 200px;
        }
    }

    &__title {
        margin-bottom: 20px;
        white-space: nowrap;

        br {
            display: none;

            @include below(600px) {
                display: block;
            }

        }
    }

    &__btn {
        padding: 23px 50px;
        border-color: transparent;

        @include below(1024px) {
            padding: 17px 40px;
        }

        @include below(440px) {
            padding: 10px 30px;
        }
    }
}