@import "main-bx";

.brands {
    padding: 70px 0;

    @include below(900px) {
        padding: 25px 0;
        overflow: hidden;
    }

    &__wrap {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin: -30px -9px 0;

        @include below(1024px) {
            margin: -20px -7px 0;
        }
    }

    &__item {
        width: calc(12.5% - 18px);
        margin: 30px 9px 0;
        text-align: center;

        @include below(1240px) {
            width: calc(16.6666% - 18px);
        }

        @include below(1024px) {
            width: calc(25% - 14px);
            margin: 20px 7px 0;
        }

        @include below_and_above(900px, 601px) {
            width: calc(20% - 14px);
            &:not(:nth-child(-n+5)) {
                display: none;
            }
        }

        @include below_and_above(600px, 451px) {
            width: calc(25% - 14px);
            &:not(:nth-child(-n+4)) {
                display: none;
            }
        }

        @include below_and_above(450px, 351px) {
            width: calc(33.33333% - 14px);
            &:not(:nth-child(-n+3)) {
                display: none;
            }
        }
        

        @include below(350px) {
            width: calc(50% - 14px);
            &:not(:nth-child(-n+2)) {
                display: none;
            }
        }
    }

    &__img {
        
        img {
            height: auto;
        }
    }

    &__name {
        font-weight: 700;
    }
}