@import "main-bx";

.cars {
    background-image: url("../img/content/cars/cars__banner.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    &__content {
        padding: 100px 0;

        @include below(768px) {
            padding: 25px 0;
        }
    }
    &__wrap {
        width: 100%;
        max-width: 612px;
        background-color: $white_color;
        padding: 40px 60px;
        border-radius: 10px;

        @include below(1240px) {
            margin: 0 auto;
        }

        @include below(768px) {
            padding: 20px 30px;
        }

        @include below(575px) {
            padding: 10px;
        }
    }

    &__title {
        margin-bottom: 25px;
        br {
            @include below(768px) {
                display: none;
            }
        }

        @include below(768px) {
            margin-bottom: 15px;
        }
       
    }

    &__text {
        margin-bottom: 15px;

        br {
            @include below(575px) {
                display: none;
            }
        }
    }

    &__phone {
        margin-bottom: 25px;

        @include below(768px) {
            margin-bottom: 20px;
        }
    }

    &__phone-link {
        color: $dark_color;
    }

    &__form-row {
        display: flex;
        margin-bottom: 16px;

        @include below(575px) {
            flex-direction: column;
            margin-bottom: 10px;
        }
    }

    &__form-input {
        padding: 15px 17px;
        border: 1px solid #231F20;
        border-radius: 10px;
        margin-right: 10px;

        @include below(768px) {
            padding: 12px;
        }

        @include below(575px) {
            margin-right: 0;
            margin-bottom: 10px;
        }

    }

    &__form-text {
        line-height: 18px;

        a {
            color: $dark_color;
            text-decoration: underline;
            transition: $trns;
            

            &:hover {
                text-decoration: none;
            }
        }
    }

    &__form-btn {

    }
}