@import "main-bx";

.product {

    &__container.container {
        padding-top: 63px;

        @media (max-width: 1240px), (max-width: 1024px), (max-width: 768px) {
            padding-top: 30px;
        }
    }

    &__wrap {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    &__col {
        &:nth-of-type(1) {
            width: 25.17%;
            margin-right: 3.6%;

            @include below(1024px) {
                width: 38%;
                margin-right: 0;
            }

            @include below(768px) {
                width: 100%;
            }
        }


        &:nth-of-type(2) {
            width: 37.89%;

            @include below(1240px) {
                width: 58%;
            }

            @include below(768px) {
                display: none !important;
            }
        }

        &:nth-last-of-type(1) {
            width: 30.47%;
            margin-left: 2.8%;

            @include below(1240px) {
                width: 100%;
                margin-left: 0;
                margin-top: 24px;
            }
        }
    }

    &__title {
        font-weight: 700;
        font-size: 60px;
        line-height: 145.5%;
        color: #231f20;
        width: 100%;
        text-transform: uppercase;

        @include below(1240px) {
            font-size: 50px;
            line-height: 140%;
        }

        @include below(1024px) {
            font-size: 40px;
            margin-bottom: 10px;
        }
    }

    &__text {
        font-weight: 700;
        font-size: 18px;
        line-height: 25px;

        @include below(1024px) {
            font-size: 16px;
            line-height: 120%;
        }
    }

    &__price {
        margin-bottom: 13px;
        display: flex;
        align-items: flex-end;
        white-space: nowrap;

        strong {
            font-weight: 700;
            font-size: 40px;
            line-height: 145.5%;
            text-transform: uppercase;
            margin-right: 36px;

            @include below(1380px) {
                font-size: 32px;
                line-height: 130%;
            }

            @include below(1024px) {
                font-size: 24px;
            }
        }

        del {
            font-size: 22px;
            line-height: 30px;
            text-decoration: line-through;
            margin-left: auto;
            margin-bottom: 6px;

            @include below(1024px) {
                font-size: 18px;
                line-height: 120%;
            }
        }
    }

    &__characteristic {
        margin-bottom: 40px;

        @include below(1240px) {
            margin-bottom: 24px;
        }
    }

    &__characteristic-list {
        display: flex;
        padding-top: 15px;
        margin: 0 -10px;

        li {
            padding: 0 10px;
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
            position: relative;
            width: 100%;
            max-width: 100%;

            &:not(:last-child)::after {
                content: "";
            }

            &::after {
                position: absolute;
                top: 50%;
                right: 0;
                transform: translateY(-50%);
                width: 1px;
                height: 20px;
                background-color: #ccc;
            }

        }

        li strong {
            font-size: 24px;
            line-height: 33px;

            @include below(1024px) {
                font-size: 18px;
                line-height: 120%;
            }
        }

        li span {
            font-size: 14px;
            line-height: 19px;
        }
    }

    &__colors {
        margin-bottom: 40px;

        @include below(1240px) {
            margin-bottom: 24px;
        }
    }

    &__colors-list {
        display: flex;
        align-items: center;
        margin-top: 15px;

        li {
            width: 45px;
            height: 45px;
            border-radius: 50%;
            cursor: pointer;
            transition: all .3s linear;

            @include below(768px) {
                width: 32px;
                height: 32px;
            }


            &:not(:last-child) {
                margin-right: 15px;
            }

            &.active {
                box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
            }

            &[data-color="red"] {
                background: #b70909;
                border: 1px solid #b70909;
            }
            &[data-color="white"] {
                background: #fff;
                border: 1px solid #ccc;
            }
            &[data-color="black"] {
                background: #000;
                border: 1px solid #000;
            }
            &[data-color="blue"] {
                background: #093ab7;
                border: 1px solid #093ab7;
            }
        }

    }

    &__btn {
        width: 100%;

        @include below(520px) {
            max-width: 300px;
        }
    }

    &__images {
        &:not(.product__images_mobile) {
            @include below(768px) {
                display: none !important;
            }
        }

        &.product__images_mobile {
            margin-bottom: 24px;
            @include above(769px) {
                display: none !important;
            }
        }

        img {
            border-radius: 20px;
            display: block;
        }
    }

    &__card {
        background-color: #f9f9f9;
        display: flex;
        flex-direction: column;
        align-items: center;
        border-radius: 20px;
        padding: 50px 30px;

        .product__text {
            text-align: center;
        }

        .product__btn {
            @include above(1241px) {
                max-width: 100%;
            }
        }


        @include below(1380px) {
            padding: 25px;
        }

        @include below(420px) {
            padding: 20px 15px;
        }
    }

    &__card-title {
        font-weight: 700;
        font-size: 35px;
        line-height: 120%;
        text-align: center;
        text-transform: uppercase;
        margin-bottom: 10px;

        @include below(1024px) {
            font-size: 24px;
        }
    }

    &__timer {
        margin: 30px 0;

        @include below(420px) {
            margin: 24px 0;
        }

        .timer-wrap {
            display: flex;
            align-items: center;
            margin: 0 -5px;
        }

        .timer-item {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            width: 92px;
            height: 92px;

            background-color: #eee;
            border-radius: 10px;
            margin: 0 5px;
            padding: 8px;

            @include below(1380px) {
                width: 72px;
                height: 72px;
            }
            
            @include below(420px) {
                width: 52px;
                height: 52px;
            }
        }

        .timer-numb {
            font-weight: 600;
            font-size: 44px;
            line-height: 60px;
            text-align: center;
            letter-spacing: 0.01em;

            @include below(1380px) {
                font-size: 32px;
                line-height: 140%;
            }

            @include below(768px) {
                font-size: 24px;
            }

            @include below(420px) {
                font-size: 18px;
            }
        }

        .timer-text {
            font-size: 12px;
            line-height: 16px;
            text-align: center;
            letter-spacing: 0.01em;

            @include below(420px) {
                font-size: 10px;
            }
        }
    }
}

