@import "main-bx";

.model {

    &__wrap {
        display: flex;
        align-items: center;
        padding: 80px 225px 80px 0;

        @include below(1700px) {
            padding: 80px 80px 80px 0;
        }

        @include below(1024px) {
            flex-direction: column;
            padding: 25px 15px;
        }
    }

    &__img {
        width: 100%;
        max-width: 1122px;

        img {
            width: 100%;
            height: auto;
        }
        @include below(1240px) {
            max-width: 800px;
        }
    }

    &__content {
        padding: 55px 50px 50px;
        background-color: $white_2_color;
        border-radius: 10px;

        @include below(1300px) {
            padding: 20px;
        }

        @include below(1024px) {
            margin-top: 20px;
        }
    } 

    &__suptitle {
        font-weight: 600;
        padding: 5px 10px;
        background-color: $light_blue_color;
        border-radius: 10px;
        display: inline-block;
        margin-bottom: 20px;
    }

    &__title {
        margin-bottom: 20px;
    }

    &__text {
        max-width: 398px;
        margin-bottom: 30px;
    }

    &__btn {
        @include below(500px) {
            width: 100%;
        }
    }
}