@import "main-bx";

.title {
    &-lg {
        text-transform: uppercase;
        font-weight: 700;
        font-size: 50px;
        line-height: 145.5%;

        @include below(1380px) {
            font-size: 40px;
            line-height: 120%;
        }

        @include below(1024px) {
            font-size: 30px;
            line-height: 100%;
        }

        @include below(400px) {
            font-size: 20px;
        }

    }

    &-md {
        text-transform: uppercase;
        font-weight: 700;
        font-size: 30px;
        line-height: 120%;

        @include below(1380px) {
            font-size: 25px;
            line-height: 120%;
        }

        @include below(1024px) {
            font-size: 20px;
        }
    }

    &-sm {
        font-weight: 700;
        font-size: 24px;
        line-height: 33px;

        @include below(768px) {
            font-size: 18px; 
            line-height: 100%;
        }
    }
}

.text {

    &-lg {
        font-weight: 700;
        font-size: 18px;
        line-height: 25px;

        @include below(768px) {
            font-size: 16px;
            line-height: 120%;
        }
    }

    &-md {
        font-size: 16px;
        line-height: 160%;

        @include below(768px) {
            font-size: 14px;
            line-height: 120%;
        }
    }

    &-sm {
        font-size: 14px;
        line-height: 23px;

        @include below(768px) {
            font-size: 12px;
            line-height: 120%;
        }
    }

    &-xsm {
        font-size: 12px;
        line-height: 23px;
    }
}