@import "main-bx";

.accordion {

    &__item {

    }

    &__head {
        font-weight: 700;
        font-size: 18px;
        line-height: 25px;
        color: #231f20;
        display: flex;
        align-items: center;
        position: relative;
        cursor: pointer;
        border-bottom: 1px solid #404040;
        padding: 20px 0 17px;

        &::before {
            content: "";
            display: block;
            background-image: url(../img/svg/icon-plus.svg);
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
            width: 16px;
            height: 16px;
            margin-right: 22px;
            transition: all .3s linear;

            .accordion__item.accordion__item_show & {
                background-image: url(../img/svg/icon-minus.svg);
            }
        }
    }

    &__body {
        max-height: 0;
        overflow: hidden;
        transition: all .3s linear;
    }
}