@import "main-bx";

.fancybox-bg {
    background: transparent !important;
}

.fancybox-inner {
    background: rgba(35, 31, 32, 0.7) !important;
}

.modal {
    display: none;
    &__container {
        &.fancybox-content {
            padding: 0;
            border-radius: 10px;
            overflow: visible !important;
        }

        .fancybox-button {
            top: -32px !important;
            right: -32px !important;
            left: auto;
            width: 32px;
            height: 32px;
        
            color: #ccc !important;
            border: 1px solid #ccc;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;

            padding: 5px !important;

            @include below(500px) {
                top: -50px !important;
                right: 0 !important;
            }
        }
    }

    &__box {
        width: 100%;
        max-width: 420px;
        margin: 0 auto;
        background-color: #fff;
        border-radius: 10px;
        padding: 60px 50px 46px;

        @include below(500px) {
            padding: 30px 15px;
        }
    }

    &__title {
        text-transform: uppercase;
        font-weight: 700;
        font-size: 30px;
        line-height: 120%;  
        margin-bottom: 25px;

        @include below(1024px) {
            font-size: 25px;
            line-height: 100%;
        }

        @include below(550px) {
            margin-bottom: 15px;
        }
    }

    &__text {
        font-size: 16px;
        line-height: 160%;  
        color: #000000;
        margin-bottom: 25px;

        @include below(550px) {
            margin-bottom: 15px;
        }
    }

    &__form-input {
        padding: 15px 17px;
        border: 1px solid #231F20;
        border-radius: 10px;
        width: 100%;
        margin-bottom: 25px;

        
        @include below(768px) {
            padding: 12px;
        }

        @include below(550px) {
            margin-bottom: 15px;
        }
    }

    &__form-btn {
        width: 100%;
    }

    &__form-text {
        margin-top: 10px;
        font-size: 12px;
        line-height: 16px;
        a {
            color: $dark_color;
            text-decoration: underline;
            transition: $trns;
            

            &:hover {
                text-decoration: none;
            }
        }
    }
}