@import "main-bx";

.btn {
    font-weight: 600;
    font-size: 15px;
    line-height: 23px;
    text-transform: uppercase;
    padding: 15px 50px;
    border: 1px solid transparent;
    border-radius: 10px;
    transition: $trns;
    white-space: nowrap;

    @include below(768px) {
        font-size: 13px;
        line-height: 21px;

        padding: 12px 25px;
    }


    &_black {
        background-color: $dark_color;
        color: $white_2_color;

        &:hover {
            background-color: #404040;;
            color: $white_2_color;
        }

        &:active {
            background-color: $dark_color;
            color: $white_2_color;
        }
    }

 

    &_white {
        background-color: $white_color;
        color: $dark_color;
        border-color: $dark_color;

        &:hover {
            background-color: $dark_color;
            color: $white_color;
          
        }

        &:active {
            background-color: $white_color;
            color: $dark_color;
            border-color: $dark_color;
        }
    }
}