@import "main-bx";

.model-card {
    background: $white_2_color;
    padding: 100px 0;

    @include below(768px) {
        padding: 25px 0;
    }

    &__wrap {
        display: flex;
        align-items: stretch;
        margin: 0 -15px;

        @include below(1380px) {
            flex-wrap: wrap;
            margin: -20px -15px 0;
        }

        @include below(768px) {
            margin: -20px -7px 0;
        }

        @include below(550px) {
            margin: -20px 0 0;
        }

    
    }

    &__item {
        width: calc(25% - 30px);
        margin: 0 15px;
        position: relative;
        

        @include below(1380px) {
            width: calc(33.33333% - 30px);
            margin: 20px 15px 0;
        }

        @include below(1240px) {
            width: calc(50% - 30px);
        }

        @include below(768px) {
            margin: 20px 7px 0;
            width: calc(50% - 14px);
        }

        @include below(550px) {
            width: 100%;
            margin: 20px 0 0;
        }
    }

    &__img {
        height: 260px;
        border-radius: 10px 10px 0px 0px;
 
        @include below_and_above(768px, 551px) {
            height: 200px;
        }

        @include below(550px) {
            height: auto;
        }

        img {
            width: 100%;
            border-radius: 10px 10px 0px 0px;
        }
    }

    &__badge {
        position: absolute;
        top: 5px;
        right: 5px;
        font-weight: 600;
        font-size: 12px;
        line-height: 16px;
        padding: 5px 10px;
        background-color: $light_blue_color;
        border-radius: 10px;
    }

    &__body {
        background-color: $white_color;
        display: flex;
        flex-direction: column;
        padding: 20px;
        border-width: 0px 1px 1px 1px;
        border-style: solid;
        border-color: #CCCCCC;
        border-radius: 0px 0px 10px 10px;

        @include below(1024px) {
            padding: 10px;
        }
    }

    &__name {
        margin-bottom: 15px;

        @include below(768px) {
            margin-bottom: 10px;
        }
    }

    &__body-wrap {
        display: flex;
    }

    &__col {
         &:last-child {
            margin-left: 36px;
         }   
    }

    &__col-top,
    &__col-bottom {
        display: flex;
        flex-direction: column;
    }

    &__col-bottom {
        margin-top: 15px;

        @include below(768px) {
            margin-top: 10px;
        }
    }

    &__text-del {
        text-decoration: line-through;
        color: #404040;
    }


    &__btn {
        width: 100%;
        margin-top: 20px;

        @include below(768px) {
            padding: 10px 50px;
        }
    }
}