@import "main-bx";

.footer {

    &__wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include below(990px) {
            padding: 25px 0 40px;
            flex-direction: column;
            align-items: flex-start;
        }
    }
    
    &__logo {
        width: 100%;
        max-width: 220px;
        display: block;

        @include below(768px) {
            max-width: 180px;
        }

        @include below(500px) {
            max-width: 140px;
        }
    }

    
    &__contacts {
        display: flex;
        align-items: center;

        @include below(990px) {
            margin-top: 15px;
            flex-direction: column;
            align-items: flex-start;
        }
    }

    &__contacts-item {
        display: flex;
        
        @include above(991px) {
            align-items: center;
           
        }

        &:first-child {
            margin-bottom: 15px;
            @include above(1025px) {
                margin-right: 42px;
            }

            @include above(991px) {
                margin-right: 20px;
                margin-bottom: 0;
            }
        }

        svg {
            width: 20px;
            height: 24px;
            margin-right: 12px;

            @include below(990px) {
                width: 19px;
                height: 19px;
                margin-right: 5px;
            }
        }

        
        a {
            color: $dark_color;
            transition: $trns;
            &:hover {
                text-decoration: underline;
            }
        }
    }

    &__contacts-text {
        font-weight: 700;    
        @include above(991px) {
            white-space: nowrap;
        }

        @include below(1024px) {
            font-size: 14px;
        }
    }

}