@import "main-bx";

.header {
    &__wrap {
        display: flex;
        justify-content: space-between;
    
        @include below(500px) {
            padding: 10px 0;
        }
    }

    &__logo {
        width: 100%;
        max-width: 220px;
        display: block;

        @include below(768px) {
            max-width: 180px;
        }

        @include below(500px) {
            max-width: 140px;
        }
    }

    &__contacts {
        display: flex;
        align-items: center;
    }

    &__address {
        display: flex;
        align-items: center;
        margin-right: 40px;

        @include below(1024px) {
            display: none;
        }

        svg {
            width: 14px;
            height: 18px;
            margin-right: 14px;
        }
    }

    &__btn {
        @include below(1024px) {
            padding: 10px 30px;
        }

        @include below(768px) {
            padding: 10px 15px;
        }

        @include below(500px) {
            padding: 10px;
            font-size: 12px;
            line-height: 20px;
            span {
                display: none;
            }
        }
    }
}